<template>
  <div>
    <div id="map"></div>
    <div class="button-group">
      <button @click="changeSize(0)">Hide</button>
      <button @click="changeSize(400)">show</button>
      <form v-on:submit.prevent="user">
      <button @click="displayMarker(markerPositions1)">내가등록한거조회</button>
      </form>
      <form v-on:submit.prevent="member">
      <button type="submit" @click="displayMarker(markerPositions2)">전체회원조회</button>
      <!-- <button type="submit">전체조회</button> -->
      <p>위도 : {{User_LATITUDE}},{{User_LONGITUDE}}</p>
    </form>
      <button @click="displayMarker([])">marker set 3 (empty)</button>
      <button @click="displayInfoWindow">infowindow</button>
    </div>
  </div>
</template>

<script>
export default {
  name: "KakaoMap",
  data:  () => ({
      map: null,
      markerPositions1: [],
      markerPositions2: [],
      markers: [],
      infowindow: null,
      rows: '',
      User_LATITUDE:'',
      User_LONGITUDE:'',
  }),
  mounted() {
    if (window.kakao && window.kakao.maps) {
      this.initMap();
    } else {
      const script = document.createElement("script");
      /* global kakao */
      script.onload = () => kakao.maps.load(this.initMap);
      script.src =
        "//dapi.kakao.com/v2/maps/sdk.js?autoload=false&appkey=9d116fa14f73b9ca6e6e3f428a03d257";
      document.head.appendChild(script);
    }
  },
  methods: {
    initMap() {
      const container = document.getElementById("map");
      const options = {
        center: new kakao.maps.LatLng(33.450701, 126.570667),
        level: 5,
      };
      this.map = new kakao.maps.Map(container, options);
    },
    changeSize(size) {
      const container = document.getElementById("map");
      container.style.width = `${size}px`;
      container.style.height = `${size}px`;
      this.map.relayout();
    },
    displayMarker(markerPositions) {
      if (this.markers.length > 0) {
        this.markers.forEach((marker) => marker.setMap(null));
      }

      const positions = markerPositions.map(
        (position) => new kakao.maps.LatLng(...position)
      );

      if (positions.length > 0) {
        this.markers = positions.map(
          (position) =>
            new kakao.maps.Marker({
              map: this.map,
              position,
            })
        );

        const bounds = positions.reduce(
          (bounds, latlng) => bounds.extend(latlng),
          new kakao.maps.LatLngBounds()
        );

        this.map.setBounds(bounds);
      }
    },
    displayInfoWindow() {
      if (this.infowindow && this.infowindow.getMap()) {
        //이미 생성한 인포윈도우가 있기 때문에 지도 중심좌표를 인포윈도우 좌표로 이동시킨다.
        this.map.setCenter(this.infowindow.getPosition());
        return;
      }

      var iwContent = '<div style="padding:5px;">Hello World!</div>', // 인포윈도우에 표출될 내용으로 HTML 문자열이나 document element가 가능합니다
        iwPosition = new kakao.maps.LatLng(33.450701, 126.570667), //인포윈도우 표시 위치입니다
        iwRemoveable = true; // removeable 속성을 ture 로 설정하면 인포윈도우를 닫을 수 있는 x버튼이 표시됩니다

      this.infowindow = new kakao.maps.InfoWindow({
        map: this.map, // 인포윈도우가 표시될 지도
        position: iwPosition,
        content: iwContent,
        removable: iwRemoveable,
      });

      this.map.setCenter(iwPosition);
    },
    user: function (event,User_LATITUDE,User_LONGITUDE){// eslint-disable-line no-unused-vars
        this.$http.post('/api/spot/user', {
           User_ID: this.$cookies.get("UserID")
      })
      console.log(this.$cookies.get("UserID"))
      .then((res) => {
       
         for (let i = 0; i<5 ; i++) { // 0, 1, 2가 출력됩니다.
  
        this.rows = res.data.rows[i]
        this.User_LATITUDE = res.data.rows[i].User_LATITUDE
        this.User_LONGITUDE = res.data.rows[i].User_LONGITUDE
        this.markerPositions2.push([this.User_LATITUDE,this.User_LONGITUDE])
        // this.markerPositions2.push([this.User_LATITUDE,this.User_LONGITUDE])
         }
      })
    },
    member: function (event){// eslint-disable-line no-unused-vars
        this.$http.post('/api/spot/member', {
      })
      .then((res) => {
         for (let i = 0; i<5 ; i++) { // 0, 1, 2가 출력됩니다.
        this.User_LATITUDE = res.data.rows[i].User_LATITUDE
        this.User_LONGITUDE = res.data.rows[i].User_LONGITUDE
        this.markerPositions2.push([this.User_LATITUDE,this.User_LONGITUDE])
        // this.markerPositions2.push([this.User_LATITUDE,this.User_LONGITUDE])
         }
      })
    }
  }   
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
#map {
  width: 400px;
  height: 400px;
}

.button-group {
  margin: 10px 0px;
}

button {
  margin: 0 3px;
}
</style>
